body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
  font-family: "Raleway";
}

a {
  text-decoration: none;
  color: #000;
}

hr {
  width: 100%;
  border: 0.5px solid #777;
  margin: 0;
}

::-webkit-scrollbar {
  width: 12px;
}

::-webkit-scrollbar-track {
  background: rgba(0, 0, 0, 0);
  border-radius: 2px;
}

::-webkit-scrollbar-thumb {
  background: #ddd;
}

::-webkit-scrollbar-thumb:hover {
  background: #c9c9c9;
}

.content-small-section, .content-large-section {
  background-color: #fff;
  border: 0.5px solid #777;
  border-radius: 6px;
}

#skills, #resume, #projects, #experience, #contact, #about {
  background-attachment: fixed;
  background-position: center;
  background-size: cover;
  background-repeat: repeat-y;
}

.hvr-underline-from-left-purple, .hvr-underline-from-left-blue, .hvr-underline-from-left-green, .hvr-underline-from-left-yellow, .hvr-underline-from-left-orange, .hvr-underline-from-left-red {
  display: flex;
  vertical-align: middle;
  -webkit-transform: perspective(1px) translateZ(0);
  transform: perspective(1px) translateZ(0);
  box-shadow: 0 0 1px rgba(0, 0, 0, 0);
  position: relative;
  overflow: hidden;
}

.hvr-underline-from-left-purple ::before, .hvr-underline-from-left-blue ::before, .hvr-underline-from-left-green ::before, .hvr-underline-from-left-yellow ::before, .hvr-underline-from-left-orange ::before, .hvr-underline-from-left-red ::before {
  content: "";
  position: absolute;
  z-index: -1;
  left: 0;
  right: 100%;
  bottom: 0;
  height: 4px;
  transition-property: right;
  transition-duration: 0.3s;
  transition-timing-function: ease-out;
}

.hvr-underline-from-left-purple :hover::before, .hvr-underline-from-left-purple :focus::before, .hvr-underline-from-left-purple :active::before, .hvr-underline-from-left-blue :hover::before, .hvr-underline-from-left-blue :focus::before, .hvr-underline-from-left-blue :active::before, .hvr-underline-from-left-green :hover::before, .hvr-underline-from-left-green :focus::before, .hvr-underline-from-left-green :active::before, .hvr-underline-from-left-yellow :hover::before, .hvr-underline-from-left-yellow :focus::before, .hvr-underline-from-left-yellow :active::before, .hvr-underline-from-left-orange :hover::before, .hvr-underline-from-left-orange :focus::before, .hvr-underline-from-left-orange :active::before, .hvr-underline-from-left-red :hover::before, .hvr-underline-from-left-red :focus::before, .hvr-underline-from-left-red :active::before {
  right: 0;
}

.contact-row {
  position: relative;
  width: auto;
  margin: 5px auto;
  justify-content: center;
}
.contact-row h2 {
  height: -webkit-min-content;
  height: -moz-min-content;
  height: min-content;
  margin: auto 0;
  font-size: 120%;
}
.contact-row .contact-icon {
  font-size: 100px;
  margin-right: 30px;
}
.contact-row .contact-icon-small {
  position: absolute;
  bottom: 5px;
  right: 1%;
  font-size: 20px;
  color: rgba(0, 0, 0, 0.3);
}
.contact-row .fa-envelope-square {
  color: #f1d592;
}
.contact-row .fa-linkedin {
  color: #0077B5;
}

.content-large-section {
  width: 80%;
  margin: 20px auto;
}

.content-small-section {
  width: 23%;
  min-width: 120px;
  max-width: 163.77px;
  height: auto;
  padding: 6px;
  margin: 0 40px;
}

.experience-header {
  width: auto;
  margin: 0 auto;
  justify-content: center;
}

.flex-col {
  display: flex;
  flex-direction: column;
}

.flex-row {
  display: flex;
  flex-direction: row;
}

.hamburger {
  display: none;
  position: fixed;
  top: 15px;
  left: 3vw;
  z-index: 3;
  font-size: 40px;
  color: rgba(0, 0, 0, 0.45);
}
.hamburger :hover {
  color: rgba(0, 0, 0, 0.8);
}

.hvr-underline-from-left-red ::before {
  background: #f00;
}
.hvr-underline-from-left-orange ::before {
  background: #ff7100;
}
.hvr-underline-from-left-yellow ::before {
  background: #ffe000;
}
.hvr-underline-from-left-green ::before {
  background: #07ff00;
}
.hvr-underline-from-left-blue ::before {
  background: #0041ff;
}
.hvr-underline-from-left-purple ::before {
  background: #b100ff;
}
.info-title {
  display: flex;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  flex-direction: column;
  height: 70px;
  background-size: cover;
  z-index: 1;
}

.info-title p {
  font-size: 40px;
  color: #fff;
  margin: auto 10px;
}

.mobileResumePdf {
  display: none;
}

.nav-hover-text {
  display: flex;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  text-align: right;
  align-items: center;
  justify-content: center;
}

.nav-tab {
  flex: 1 1;
  font-size: 120%;
  cursor: pointer;
  margin: 0;
}

.nav-tab section {
  width: auto;
  height: auto;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.project-entry {
  flex: 1 1;
}

.project-img, .project-video {
  width: 80%;
  border: 1px solid #777;
  box-shadow: 0 0 4px 0 #444;
  margin: 0 auto;
}

.project-row {
  display: flex;
  width: 90%;
  margin: 10px auto;
  justify-content: space-around;
}

.resume-holder {
  height: 90vh;
}

.skill-img {
  width: 80%;
  height: auto;
  object-fit: contain;
  margin: 0 auto;
}

.skill-row {
  width: 90%;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  padding-bottom: 6px;
  margin: 20px auto;
  overflow-x: scroll;
}

.skill-text {
  margin: 10px 0;
}

.small-img {
  width: 30px;
  height: 30px;
  margin: auto 10px auto 0px;
}

.standard-margin {
  width: 95%;
  margin: 10px auto;
}

.text-center {
  text-align: center;
  padding: 0px 10px;
}

#about {
  background-image: url(/WebPortfolio/static/media/graph-1.15997b0b.png);
}

#contact {
  background-image: url(/WebPortfolio/static/media/graph-5.6847f6aa.png);
}

#experience {
  background-image: url(/WebPortfolio/static/media/graph-2.ed4e6149.png);
}

#grayOverlay {
  display: none;
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background-color: rgba(51, 51, 51, 0.9);
  z-index: 2;
}

#info {
  width: 90%;
  margin-left: auto;
}

#nav-bar {
  position: fixed;
  top: 0;
  left: 0;
  width: 10%;
  height: 100%;
  background-color: #fff;
  border-right: 1px solid #777;
  box-shadow: 0 0 5px 0 #111;
  align-content: space-around;
}

#nav-desc {
  flex: 1 1;
  font-size: 0.7vw;
  text-align: right;
  margin: 0;
}

#nav-name {
  flex: 0 0 auto;
  margin: 15px;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  cursor: pointer;
}

#nav-title {
  flex: 2 1;
  font-size: 1.8vw;
  text-align: right;
  margin: 0;
}

#projects {
  background-image: url(/WebPortfolio/static/media/graph-3.e77ce32c.png);
}

#resume {
  background-image: url(/WebPortfolio/static/media/graph-6.652a2468.png);
}

#skills {
  background-image: url(/WebPortfolio/static/media/graph-4.9c545a8e.png);
}

@media all and (max-width: 1050px) {
  hr {
    display: none;
  }

  .hamburger {
    display: flex;
  }

  .info-title p {
    text-align: right;
  }

  .nav-tab {
    font-size: 24px;
  }

  #info {
    width: 100%;
    margin-left: 0px;
  }

  #nav-bar {
    display: none;
    width: 60%;
    z-index: 3;
  }

  #nav-desc {
    font-size: 170%;
  }

  #nav-name {
    flex: 0 0 30vh;
  }
}
@media all and (max-width: 850px) {
  .project-row {
    flex-direction: column;
  }
}
@media all and (max-width: 700px) {
  .contact-row h2 {
    font-size: 80%;
  }

  .experience-header {
    flex-direction: column-reverse;
  }

  .small-img {
    margin: 0px auto;
  }
}

